import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select, Checkbox } from 'antd';
import {
  getPaperEnrollmentById,
  updatePaperEnrollment,
  createPaperEnrollment,
  getTrainTypes,
  getPaperType,
} from 'services/PaperEnrollment';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    level: '',
    type: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formTypeDisciplines' })
class FormTypeDisciplinesPage extends Component {
  state = {
    typeDisciplinesId: false,
    initialValue: {},
    PaperType: [],
    TrainTypes: [],
  };

  componentWillMount() {
    const { match } = this.props;
    this.getDataForm();
    if (match.params.id) {
      this.setState({ typeDisciplinesId: match.params.id });
      this.getDataById(match.params.id);
    }
  }

  getDataForm = async () => {
    const TrainTypes = await getTrainTypes();
    const PaperType = await getPaperType();

    this.setState({
      PaperType: PaperType.data.items,
      TrainTypes: TrainTypes.data.items,
    });
  };

  getDataById = async id => {
    const res = await getPaperEnrollmentById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { typeDisciplinesId } = this.state;
    try {
      if (!typeDisciplinesId) {
        await createPaperEnrollment(values);
      } else {
        await updatePaperEnrollment({ id: typeDisciplinesId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { typeDisciplinesId, PaperType, TrainTypes } = this.state;
    return (
      <Card title={typeDisciplinesId ? 'Cập nhật xử lý kỹ luật' : 'Thêm xử lý kỹ luật'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Hệ" {...formItemLayout}>
              {getFieldDecorator('trainTypeId', {
                initialValue: initialValue.trainTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {TrainTypes.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Tên giấy tờ" {...formItemLayout}>
              {getFieldDecorator('paperTypeId', {
                initialValue: initialValue.paperTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {PaperType.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ghi chú" hasFeedback>
              {getFieldDecorator('note', {
                initialValue: initialValue.note,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số thứ tự" hasFeedback>
              {getFieldDecorator('orderNo', {
                initialValue: initialValue.orderNo,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nhóm" hasFeedback>
              {getFieldDecorator('group', {
                initialValue: initialValue.group,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Chọn mặc định">
              {getFieldDecorator('isDefault', {
                initialValue: initialValue.isDefault,
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Bắt buộc">
              {getFieldDecorator('isMandantory', {
                initialValue: initialValue.isMandantory,
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTypeDisciplinesPage {...props} />
  </Page>
));



