import { request } from 'utils';

const prefix = '/api/students';

export const getPaperEnrollments = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
  trainTypeId,
  candidateId,
}) =>
  request({
    prefix,
    url: '/PaperEnrollment',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled, trainTypeId, candidateId },
    data: {},
  });

export const getPaperEnrollmentById = id =>
  request({
    prefix,
    url: `/PaperEnrollment/${id}`,
    method: 'GET',
  });

export const deletePaperEnrollment = id =>
  request({
    prefix,
    url: `/PaperEnrollment/${id}`,
    method: 'DELETE',
  });

export const updatePaperEnrollment = ({
  id,
  trainTypeId,
  paperTypeId,
  note,
  orderNo,
  group,
  isDefault = false,
  isMandantory = false,
}) =>
  request({
    prefix,
    url: `/PaperEnrollment/${id}`,
    data: { id, trainTypeId, paperTypeId, note, orderNo, group, isDefault, isMandantory },
    method: 'PUT',
  });

export const createPaperEnrollment = ({
  trainTypeId,
  paperTypeId,
  note,
  orderNo,
  group,
  isDefault = false,
  isMandantory = false,
}) =>
  request({
    prefix,
    url: `/PaperEnrollment`,
    data: { trainTypeId, paperTypeId, note, orderNo, group, isDefault, isMandantory },
    method: 'POST',
  });

export const getTrainTypes = ({
  label,
  labelInEnglish,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix: '/api/categories',
    url: `/TrainType`,
    method: 'GET',
    params: { label, labelInEnglish, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getPaperType = ({
  label,
  labelInEnglish,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix: '/api/categories',
    url: `/PaperType`,
    method: 'GET',
    params: { label, labelInEnglish, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



